<!--
  This is a Vue Single File Component (SFC) which contains three sections:
  1. <template> - The HTML/markup section using Vue's template syntax
  2. <script> - The JavaScript/Vue logic section
  3. <style> - The CSS/styling section (using Tailwind CSS utility classes)
-->

<template>
  <!-- Main container with full height and gray background -->
  <div class="min-h-screen bg-gray-100">
    <!-- Header section with workout name and close button -->
    <header class="bg-white shadow-sm">
      <div class="max-w-7xl mx-auto px-4 py-4 sm:px-6 lg:px-8 flex items-center justify-between">
        <h1 class="text-2xl font-display font-bold text-gray-900">{{ workout.name || 'Workout' }}</h1>
        <button
          @click="$emit('close')"
          class="text-gray-400 hover:text-gray-500"
        >
          <XIcon class="h-6 w-6" />
        </button>
      </div>
    </header>

    <!-- Main content area -->
    <main class="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      <div class="bg-white shadow rounded-lg overflow-hidden">
        <!-- Current Exercise Section -->
        <div class="p-6">
          <!-- Show this section if there's a current exercise or we're in exercise rest -->
          <div v-if="currentExercise || isExerciseRest" class="space-y-6">
            <!-- Exercise/Rest Information Display -->
            <div class="text-center">
              <h2 class="text-2xl font-display font-semibold text-gray-900">
                {{ currentExercise.name }}
              </h2>
              <p class="mt-1 text-lg text-gray-600">
                {{ getSetText() }}
              </p>
              <!-- Only show reps when actively doing an exercise -->
              <p v-if="!isResting && !isExerciseRest" class="text-sm text-gray-500">
                {{ currentExercise.reps }} reps
              </p>
            </div>

            <!-- Timer Display - Shows during rest periods -->
            <div v-if="isResting" class="text-center">
              <div class="text-6xl font-display font-bold" :class="timerClasses">
                {{ formatTime(timeRemaining) }}
              </div>
              <p class="mt-2 text-gray-600">
                {{ isExerciseRest ? 'Rest before next exercise' : 'Rest between sets' }}
                <span v-if="isPaused" class="text-yellow-600 font-medium"> (PAUSED)</span>
              </p>
              
              <!-- Pause/Resume Button -->
              <div class="mt-4">
                <button
                  @click="togglePause"
                  class="px-6 py-3 text-lg font-medium text-white bg-primary hover:bg-primary-dark rounded-md shadow-sm transition-colors flex items-center space-x-2 mx-auto"
                  :title="isPaused ? 'Resume' : 'Pause'"
                >
                  <PauseIcon v-if="!isPaused" class="h-5 w-5" />
                  <PlayIcon v-else class="h-5 w-5" />
                  <span>{{ isPaused ? 'Resume' : 'Pause' }}</span>
                </button>
              </div>
              
              <!-- Rest Action Buttons -->
              <div class="mt-4 flex justify-center space-x-4">
                <!-- Skip Rest Button -->
                <button
                  @click="skipRest"
                  class="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-200 hover:bg-gray-300 rounded-md shadow-sm transition-colors flex items-center space-x-2"
                  title="Skip rest period"
                >
                  <FastForwardIcon class="h-4 w-4" />
                  <span>Skip Rest</span>
                </button>
                
                <!-- Skip Set Button (during set rest) -->
                <button
                  v-if="!isExerciseRest && currentSet < currentExercise.count"
                  @click="skipSet"
                  class="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-200 hover:bg-gray-300 rounded-md shadow-sm transition-colors flex items-center space-x-2"
                  title="Skip to next set"
                >
                  <ArrowRightIcon class="h-4 w-4" />
                  <span>Skip Set</span>
                </button>
                
                <!-- Skip Exercise Button -->
                <button
                  v-if="currentExerciseIndex < exercises.length - 1"
                  @click="skipExercise"
                  class="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-200 hover:bg-gray-300 rounded-md shadow-sm transition-colors flex items-center space-x-2"
                  title="Skip to next exercise"
                >
                  <FastForwardIcon class="h-4 w-4" />
                  <span>Skip Exercise</span>
                </button>
              </div>
            </div>

            <!-- Action Button - Start/Finish Set -->
            <div class="flex justify-center space-x-4">
              <button
                v-if="!isResting"
                @click="handleActionButton"
                class="px-6 py-3 text-lg font-medium text-white bg-primary hover:bg-primary-dark rounded-md shadow-sm transition-colors"
              >
                {{ getButtonText() }}
              </button>
              
              <!-- Skip Set Button -->
              <button
                v-if="!isResting && hasStartedExercise && currentSet < currentExercise.count"
                @click="skipSet"
                class="px-4 py-3 text-lg font-medium text-gray-700 bg-gray-200 hover:bg-gray-300 rounded-md shadow-sm transition-colors flex items-center space-x-2"
                title="Skip to next set"
              >
                <ArrowRightIcon class="h-5 w-5" />
                <span>Skip Set</span>
              </button>
              
              <!-- Skip Exercise Button -->
              <button
                v-if="!isResting && currentExerciseIndex < exercises.length - 1"
                @click="skipExercise"
                class="px-4 py-3 text-lg font-medium text-gray-700 bg-gray-200 hover:bg-gray-300 rounded-md shadow-sm transition-colors flex items-center space-x-2"
                title="Skip to next exercise"
              >
                <FastForwardIcon class="h-5 w-5" />
                <span>Skip Exercise</span>
              </button>
            </div>
          </div>

          <!-- Workout Complete Screen -->
          <div v-else class="text-center py-12">
            <div class="text-primary mb-4">
              <CheckCircleIcon class="h-16 w-16 mx-auto" />
            </div>
            <h2 class="text-3xl font-display font-bold text-gray-900">
              Workout Complete!
            </h2>
            <p class="mt-2 text-gray-600">Great job! You've finished your workout.</p>
            <div class="mt-6 space-x-4">
              <button
                @click="$emit('close')"
                class="px-6 py-3 text-lg font-medium text-white bg-primary hover:bg-primary-dark rounded-md shadow-sm transition-colors"
              >
                Done
              </button>
            </div>
          </div>
        </div>

        <!-- Progress Section - Shows all exercises and their status -->
        <div class="border-t border-gray-200 p-6 bg-gray-50">
          <h3 class="text-sm font-medium text-gray-500 mb-4">Progress</h3>
          <div class="space-y-3">
            <template v-for="(item, index) in workout.sets" :key="item.id">
              <!-- Exercise Progress Item -->
              <div v-if="item.type === 'exercise'" class="flex items-center text-sm">
                <div
                  class="flex-shrink-0 w-5 h-5 rounded-full mr-3 flex items-center justify-center"
                  :class="getExerciseStatusClass(Math.floor(index/2))"
                >
                  <CheckIcon v-if="Math.floor(index/2) < currentExerciseIndex" class="w-3 h-3 text-white" />
                  <div v-else-if="Math.floor(index/2) === currentExerciseIndex" class="w-2 h-2 bg-current rounded-full" />
                </div>
                <span 
                  :class="getExerciseTextClass(Math.floor(index/2))"
                  @click="handleExerciseClick(Math.floor(index/2), item.name)"
                  class="cursor-pointer hover:text-primary transition-colors"
                  :title="Math.floor(index/2) === currentExerciseIndex ? 'Current exercise' : `Click to skip to ${item.name}`"
                >{{ item.name }}</span>
              </div>
              <!-- Rest Period Progress Item -->
              <div v-else class="flex items-center text-sm pl-8">
                <span class="text-secondary italic" :class="{
                  'line-through': Math.floor(index/2) < currentExerciseIndex,
                  'font-medium': Math.floor(index/2) === currentExerciseIndex && isExerciseRest
                }">
                  Rest: {{ formatTime(item.restTime) }}
                </span>
              </div>
            </template>
          </div>
        </div>
      </div>
    </main>

    <!-- Skip Confirmation Modal -->
    <ConfirmationModal
      v-if="showSkipConfirmation"
      title="Skip to Exercise"
      :message="skipConfirmationMessage"
      confirm-text="Skip"
      cancel-text="Cancel"
      confirm-variant="primary"
      @confirm="confirmSkipToExercise"
      @cancel="cancelSkipToExercise"
    />
  </div>
</template>

<script setup>
// Vue 3 Composition API imports
import { ref, computed, onMounted, onUnmounted } from 'vue'
// Heroicons imports for UI icons (from @heroicons/vue package)
import { XIcon, CheckIcon, CheckCircleIcon, ArrowRightIcon, FastForwardIcon, PauseIcon, PlayIcon } from '@heroicons/vue/solid'
// Import ConfirmationModal component
import ConfirmationModal from './ConfirmationModal.vue'

// Props definition - workout object is required from parent component
const props = defineProps({
  workout: {
    type: Object,
    required: true
  }
})

// Emit events to parent component
defineEmits(['close'])

// Audio setup for workout sounds
const alarmSound = new Audio()
const tickingSound = new Audio()
let soundsLoaded = false

// Component lifecycle hook - runs when component is mounted
onMounted(() => {
  // Initialize sound files
  try {
    alarmSound.src = './sounds/alarm.mp3'
    tickingSound.src = './sounds/ticking.mp3'
    
    // Add error handlers for sound loading failures
    alarmSound.onerror = () => console.log('Alarm sound failed to load')
    tickingSound.onerror = () => console.log('Ticking sound failed to load')
    
    // Configure sound behavior
    alarmSound.loop = true    // Alarm will keep playing until stopped
    tickingSound.loop = true  // Ticking will continue until stopped
    
    // Add load success handlers
    alarmSound.oncanplaythrough = () => {
      alarmSound.oncanplaythrough = null
      console.log('Alarm sound loaded')
    }
    tickingSound.oncanplaythrough = () => {
      tickingSound.oncanplaythrough = null
      console.log('Ticking sound loaded')
    }
    
    soundsLoaded = true
  } catch (error) {
    console.log('Error setting up sounds:', error)
    soundsLoaded = false
  }
})

// State management using Vue's ref for reactivity
const currentExerciseIndex = ref(0)  // Tracks which exercise we're on
const currentSet = ref(1)            // Tracks which set we're on in current exercise
const isResting = ref(false)         // Whether we're in a rest period
const timeRemaining = ref(0)         // Seconds remaining in current timer
const isExerciseRest = ref(false)    // Whether we're in a rest between exercises
const hasStartedExercise = ref(false) // Whether current set has been started
const isPaused = ref(false)          // Whether the timer is paused
let timerInterval = null             // Holds the timer interval reference

// Skip confirmation modal state
const showSkipConfirmation = ref(false)
const exerciseToSkipTo = ref(null)

// Computed properties - reactive data that depends on other reactive data
const exercises = computed(() => {
  // Filter workout sets to get only exercise items (not rest periods)
  return props.workout.sets.filter(set => set.type === 'exercise')
})

const currentExercise = computed(() => {
  // Get the current exercise or null if workout is complete
  const exerciseIndex = Math.floor(currentExerciseIndex.value)
  return exerciseIndex < exercises.value.length
    ? exercises.value[exerciseIndex]
    : null
})

// Computed classes for timer display based on time remaining
const timerClasses = computed(() => ({
  'text-primary': timeRemaining.value > 5,
  'text-yellow-500': timeRemaining.value <= 5 && timeRemaining.value > 0,
  'text-red-500': timeRemaining.value === 0
}))

// Computed property for skip confirmation message
const skipConfirmationMessage = computed(() => {
  if (!exerciseToSkipTo.value) return ''
  
  const isSkippingForward = exerciseToSkipTo.value.index > currentExerciseIndex.value
  const direction = isSkippingForward ? 'forward to' : 'back to'
  
  return `Are you sure you want to skip ${direction} "${exerciseToSkipTo.value.name}"?`
})

// Utility Methods
const formatTime = (seconds) => {
  // Convert seconds to MM:SS format
  const mins = Math.floor(seconds / 60)
  const secs = seconds % 60
  return `${mins}:${secs.toString().padStart(2, '0')}`
}

// UI Helper Methods
const getExerciseStatusClass = (index) => {
  // Return appropriate CSS classes for exercise status indicators
  const exerciseIndex = Math.floor(index)
  if (exerciseIndex < currentExerciseIndex.value) {
    return 'bg-green-500'  // Completed exercise
  }
  if (exerciseIndex === currentExerciseIndex.value) {
    return 'border-2 border-primary text-primary'  // Current exercise
  }
  return 'border-2 border-gray-300 text-gray-300'  // Future exercise
}

const getExerciseTextClass = (index) => {
  // Return appropriate CSS classes for exercise text
  const exerciseIndex = Math.floor(index)
  if (exerciseIndex < currentExerciseIndex.value) {
    return 'text-gray-500 line-through'  // Completed exercise
  }
  if (exerciseIndex === currentExerciseIndex.value) {
    return 'text-gray-900 font-medium'   // Current exercise
  }
  return 'text-gray-500'                 // Future exercise
}

// Sound Management Methods
const stopAllSounds = () => {
  if (!soundsLoaded) return
  
  try {
    alarmSound.pause()
    alarmSound.currentTime = 0
    tickingSound.pause()
    tickingSound.currentTime = 0
  } catch (error) {
    console.log('Error stopping sounds:', error)
  }
}

const playSound = (type) => {
  if (!soundsLoaded) return
  
  try {
    if (type === 'warning') {
      // Start ticking sound at 5 seconds remaining
      tickingSound.currentTime = 0
      tickingSound.play().catch(error => {
        console.log('Error playing ticking sound:', error)
      })
    } else if (type === 'complete') {
      // Stop ticking and start alarm at 0 seconds
      tickingSound.pause()
      tickingSound.currentTime = 0
      alarmSound.currentTime = 0
      alarmSound.play().catch(error => {
        console.log('Error playing alarm sound:', error)
      })
    }
  } catch (error) {
    console.log(`Error attempting to play ${type} sound:`, error)
  }
}

// Text Display Methods
const getSetText = () => {
  if (!currentExercise.value) return ''
  
  if (isResting.value) {
    if (isExerciseRest.value) {
      return 'Rest before next exercise'
    }
    return `Rest after Set ${currentSet.value - 1}`
  }
  
  return `${currentExercise.value.name} - Set ${currentSet.value} of ${currentExercise.value.count}`
}

const getButtonText = () => {
  if (!hasStartedExercise.value) {
    return `Start Set ${currentSet.value}`
  }
  return `Finish Set ${currentSet.value}`
}

// Timer Management
const startTimer = (duration) => {
  isResting.value = true
  timeRemaining.value = duration
  isPaused.value = false
  
  timerInterval = setInterval(() => {
    if (!isPaused.value && timeRemaining.value > 0) {
      timeRemaining.value--
      
      // Sound cues
      if (timeRemaining.value === 5) {
        playSound('warning')  // Start ticking
      }
      
      if (timeRemaining.value === 0) {
        clearInterval(timerInterval)
        playSound('complete')  // Start alarm
        
        if (isExerciseRest.value) {
          // Moving to next exercise
          isExerciseRest.value = false
          isResting.value = false
          hasStartedExercise.value = false
        } else {
          // Rest between sets is over
          isResting.value = false
          hasStartedExercise.value = false
        }
      }
    }
  }, 1000)  // Update every second
}

// Exercise Flow Control Methods
const startSet = () => {
  stopAllSounds()
  hasStartedExercise.value = true
}

const finishSet = () => {
  stopAllSounds()
  
  if (currentSet.value < currentExercise.value.count) {
    // More sets remain in current exercise
    startTimer(currentExercise.value.restTime)
    currentSet.value++
  } else {
    // Exercise is complete
    currentSet.value = 1
    currentExerciseIndex.value++
    
    if (currentExerciseIndex.value < exercises.value.length) {
      // More exercises remain
      const currentExercisePosition = props.workout.sets.findIndex(
        (set, idx) => set.type === 'exercise' && 
        Math.floor(idx/2) === currentExerciseIndex.value - 1
      )
      
      // Check for rest period before next exercise
      const nextRestPeriod = props.workout.sets.find(
        (set, idx) => idx > currentExercisePosition && 
        idx < props.workout.sets.length && 
        set.type === 'rest'
      )
      
      if (nextRestPeriod) {
        // Start rest period before next exercise
        isExerciseRest.value = true
        hasStartedExercise.value = false
        startTimer(nextRestPeriod.restTime)
      } else {
        // No rest period, immediately ready for next exercise
        isExerciseRest.value = false
        hasStartedExercise.value = false
      }
    }
  }
}

// Button Click Handler
const handleActionButton = () => {
  if (!hasStartedExercise.value) {
    startSet()
  } else {
    finishSet()
  }
}

// Skip Set Method
const skipSet = () => {
  stopAllSounds()
  clearInterval(timerInterval)
  isResting.value = false
  hasStartedExercise.value = false
  
  if (currentSet.value < currentExercise.value.count) {
    currentSet.value++
  }
}

// Skip Exercise Method
const skipExercise = () => {
  stopAllSounds()
  
  // Reset set counter for the new exercise
  currentSet.value = 1
  currentExerciseIndex.value++
  
  if (currentExerciseIndex.value < exercises.value.length) {
    // More exercises remain
    const currentExercisePosition = props.workout.sets.findIndex(
      (set, idx) => set.type === 'exercise' && 
      Math.floor(idx/2) === currentExerciseIndex.value - 1
    )
    
    // Check for rest period before next exercise
    const nextRestPeriod = props.workout.sets.find(
      (set, idx) => idx > currentExercisePosition && 
      idx < props.workout.sets.length && 
      set.type === 'rest'
    )
    
    if (nextRestPeriod) {
      // Start rest period before next exercise
      isExerciseRest.value = true
      hasStartedExercise.value = false
      startTimer(nextRestPeriod.restTime)
    } else {
      // No rest period, immediately ready for next exercise
      isExerciseRest.value = false
      hasStartedExercise.value = false
    }
  }
}

// Skip Rest Method
const skipRest = () => {
  stopAllSounds()
  clearInterval(timerInterval)
  
  if (isExerciseRest.value) {
    // Skipping rest between exercises - move to next exercise
    isExerciseRest.value = false
    isResting.value = false
    hasStartedExercise.value = false
  } else {
    // Skipping rest between sets - move to next set
    isResting.value = false
    hasStartedExercise.value = false
  }
}

// Pause/Resume Timer
const togglePause = () => {
  if (isPaused.value) {
    // Resume timer
    isPaused.value = false
    if (isResting.value) {
      // Resume the rest timer
      startTimer(timeRemaining.value)
    }
  } else {
    // Pause timer
    isPaused.value = true
    clearInterval(timerInterval)
    stopAllSounds()
  }
}

// Skip to Exercise Methods
const handleExerciseClick = (exerciseIndex, exerciseName) => {
  // Don't allow skipping to current exercise
  if (exerciseIndex === currentExerciseIndex.value) {
    return
  }
  
  // Set the exercise to skip to and show confirmation modal
  exerciseToSkipTo.value = {
    index: exerciseIndex,
    name: exerciseName
  }
  showSkipConfirmation.value = true
}

const confirmSkipToExercise = () => {
  if (!exerciseToSkipTo.value) return
  
  stopAllSounds()
  clearInterval(timerInterval)
  
  // Skip to the selected exercise
  currentExerciseIndex.value = exerciseToSkipTo.value.index
  currentSet.value = 1
  isResting.value = false
  isExerciseRest.value = false
  hasStartedExercise.value = false
  isPaused.value = false
  
  // Close the modal
  showSkipConfirmation.value = false
  exerciseToSkipTo.value = null
}

const cancelSkipToExercise = () => {
  showSkipConfirmation.value = false
  exerciseToSkipTo.value = null
}

// Cleanup when component is destroyed
onUnmounted(() => {
  // Clear any running timers
  if (timerInterval) {
    clearInterval(timerInterval)
  }
  
  // Clean up audio resources
  if (soundsLoaded) {
    stopAllSounds()
    alarmSound.src = ''
    tickingSound.src = ''
  }
})
</script> 