<template>
  <div class="min-h-screen bg-gray-100">
    <template v-if="!activeWorkout">
      <!-- Header -->
      <header class="bg-white shadow-sm">
        <div class="max-w-7xl mx-auto px-4 py-4 sm:px-6 lg:px-8">
          <h1 class="text-2xl font-display font-bold text-gray-900">Workout Timer</h1>
        </div>
      </header>

      <!-- Main Content -->
      <main class="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <WorkoutList @start-workout="startWorkout" />
      </main>
    </template>

    <!-- Workout Tracker -->
    <WorkoutTracker
      v-else
      :workout="activeWorkout"
      @close="activeWorkout = null"
    />
  </div>
</template>

<script setup>
import { ref } from 'vue'
import WorkoutList from './components/WorkoutList.vue'
import WorkoutTracker from './components/WorkoutTracker.vue'

// State
const activeWorkout = ref(null)

// Methods
const startWorkout = (workout) => {
  console.log('Starting workout in App.vue:', workout)
  activeWorkout.value = workout
}

// Import Google Fonts
const link = document.createElement('link')
link.rel = 'stylesheet'
link.href = 'https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&family=Roboto:wght@400;500;700&display=swap'
document.head.appendChild(link)
</script>

<style>
/* Base styles */
body {
  @apply antialiased text-gray-900;
}
</style> 