/**
 * Storage utility for managing workout data persistence
 * Uses IndexedDB for structured data storage with better performance and reliability
 */

import { openDB } from 'idb'

const DB_NAME = 'workout-timer-db'
const DB_VERSION = 1
const STORE_NAME = 'workouts'

// Initialize the database
const dbPromise = openDB(DB_NAME, DB_VERSION, {
  upgrade(db) {
    // Create the workouts object store if it doesn't exist
    if (!db.objectStoreNames.contains(STORE_NAME)) {
      db.createObjectStore(STORE_NAME, { keyPath: 'id' })
    }
  },
})

/**
 * Save a workout to the database
 * @param {Object} workout - The workout object to save
 * @returns {Promise<void>}
 */
export async function saveWorkout(workout) {
  // Ensure all numeric values are properly converted
  const cleanWorkout = {
    ...workout,
    sets: workout.sets.map(item => ({
      ...item,
      id: String(item.id), // Ensure ID is a string
      restTime: Number(item.restTime),
      ...(item.type === 'exercise' ? {
        reps: Number(item.reps),
        count: Number(item.count)
      } : {})
    }))
  }
  
  const db = await dbPromise
  await db.put(STORE_NAME, cleanWorkout)
}

/**
 * Get all workouts from the database
 * @returns {Promise<Array>} Array of workout objects
 */
export async function getAllWorkouts() {
  const db = await dbPromise
  const workouts = await db.getAll(STORE_NAME)
  return workouts.map(workout => ({
    ...workout,
    sets: workout.sets.map(item => ({
      ...item,
      restTime: Number(item.restTime),
      ...(item.type === 'exercise' ? {
        reps: Number(item.reps),
        count: Number(item.count)
      } : {})
    }))
  }))
}

/**
 * Delete a workout from the database
 * @param {string} id - The ID of the workout to delete
 * @returns {Promise<void>}
 */
export async function deleteWorkout(id) {
  const db = await dbPromise
  await db.delete(STORE_NAME, id)
}

/**
 * Generate a unique ID for new workouts
 * @returns {string} A unique identifier
 */
export function generateId() {
  return Date.now().toString(36) + Math.random().toString(36).substr(2)
} 