<template>
  <div class="space-y-6">
    <!-- Add Workout Button -->
    <div class="flex justify-end space-x-2">
      <!-- Import Button -->
      <label
        class="inline-flex items-center px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary cursor-pointer"
      >
        <DownloadIcon class="h-5 w-5 mr-2" />
        Import
        <input
          type="file"
          accept=".json"
          class="hidden"
          @change="handleImport"
        >
      </label>

      <!-- Export Button -->
      <button
        @click="exportWorkouts"
        class="inline-flex items-center px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary"
      >
        <UploadIcon class="h-5 w-5 mr-2" />
        Export
      </button>

      <button
        @click="createNewWorkout"
        class="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-primary hover:bg-primary-dark focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary"
      >
        <PlusIcon class="h-5 w-5 mr-2" />
        New Workout
      </button>
    </div>

    <!-- Workouts List -->
    <div v-if="workouts.length > 0" class="space-y-4">
      <div
        v-for="workout in workouts"
        :key="workout.id"
        class="bg-white shadow rounded-lg overflow-hidden"
      >
        <div class="p-4 sm:p-6">
          <div class="flex items-center justify-between">
            <h3 class="text-lg font-display font-semibold">
              {{ workout.name || 'Untitled Workout' }}
            </h3>
            <div class="flex space-x-2">
              <button
                @click="startWorkout(workout)"
                class="text-secondary hover:text-primary"
                title="Start workout"
              >
                <PlayIcon class="h-5 w-5" />
              </button>
              <button
                @click="editWorkout(workout)"
                class="text-secondary hover:text-primary"
              >
                <PencilIcon class="h-5 w-5" />
              </button>
              <button
                @click="deleteWorkout(workout)"
                class="text-secondary hover:text-red-600"
              >
                <TrashIcon class="h-5 w-5" />
              </button>
            </div>
          </div>

          <!-- Sets Preview -->
          <div class="mt-4 space-y-2">
            <div
              v-for="(item, index) in workout.sets"
              :key="index"
              class="text-sm text-gray-600"
            >
              <template v-if="item.type === 'rest'">
                <span class="text-secondary italic">
                  Rest: {{ formatTime(item.restTime) }}
                </span>
              </template>
              <template v-else>
                {{ item.name }} - {{ item.count }} sets of {{ item.reps }} reps
                <span class="text-secondary">
                  ({{ formatTime(item.restTime) }} rest between sets)
                </span>
              </template>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Empty State -->
    <div
      v-else
      class="text-center py-12 bg-white rounded-lg shadow"
    >
      <div class="text-secondary mb-4">
        <ClipboardIcon class="h-12 w-12 mx-auto" />
      </div>
      <h3 class="font-display font-semibold">No workouts yet</h3>
      <p class="text-secondary mt-1">Create your first workout to get started</p>
    </div>

    <!-- Delete Confirmation Modal -->
    <ConfirmationModal
      v-if="showDeleteConfirmation"
      title="Delete Workout"
      :message="'Are you sure you want to delete the ' + (workoutToDelete?.name || 'untitled') + ' workout?'"
      mode="confirm"
      confirm-text="Delete"
      confirm-variant="danger"
      @confirm="confirmDelete"
      @cancel="cancelDelete"
    />

    <!-- Import Success Modal -->
    <ConfirmationModal
      v-if="showImportSuccess"
      title="Import Successful"
      :message="'Successfully imported ' + importedCount + ' workout' + (importedCount !== 1 ? 's' : '') + '.'"
      mode="alert"
      confirm-text="OK"
      confirm-variant="primary"
      @confirm="closeImportSuccess"
      @cancel="closeImportSuccess"
    />

    <!-- Workout Editor Modal -->
    <WorkoutEditor
      v-if="showEditor"
      :workout="selectedWorkout"
      @save="handleWorkoutSave"
      @close="closeEditor"
    />
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import { 
  PlusIcon, 
  PencilIcon, 
  TrashIcon, 
  ClipboardIcon, 
  DownloadIcon, 
  UploadIcon,
  PlayIcon 
} from '@heroicons/vue/solid'
import WorkoutEditor from './WorkoutEditor.vue'
import ConfirmationModal from './ConfirmationModal.vue'
import { getAllWorkouts, deleteWorkout as deleteWorkoutFromDB, generateId, saveWorkout } from '../utils/storage'

// State
const workouts = ref([])
const showEditor = ref(false)
const selectedWorkout = ref(null)
const showDeleteConfirmation = ref(false)
const workoutToDelete = ref(null)
const showImportSuccess = ref(false)
const importedCount = ref(0)

// Emits
const emit = defineEmits(['start-workout'])

// Load workouts on mount
onMounted(async () => {
  workouts.value = await getAllWorkouts()
})

// Methods
const createNewWorkout = () => {
  selectedWorkout.value = {
    id: generateId(),
    name: '',
    sets: []
  }
  showEditor.value = true
}

const editWorkout = (workout) => {
  selectedWorkout.value = { ...workout }
  showEditor.value = true
}

const handleWorkoutSave = async (workout) => {
  const index = workouts.value.findIndex(w => w.id === workout.id)
  if (index !== -1) {
    workouts.value[index] = workout
  } else {
    workouts.value.push(workout)
  }
  closeEditor()
}

const deleteWorkout = (workout) => {
  workoutToDelete.value = workout
  showDeleteConfirmation.value = true
}

const confirmDelete = async () => {
  await deleteWorkoutFromDB(workoutToDelete.value.id)
  workouts.value = workouts.value.filter(w => w.id !== workoutToDelete.value.id)
  showDeleteConfirmation.value = false
  workoutToDelete.value = null
}

const cancelDelete = () => {
  showDeleteConfirmation.value = false
  workoutToDelete.value = null
}

const closeEditor = () => {
  showEditor.value = false
  selectedWorkout.value = null
}

// Format time from seconds to minutes and seconds
const formatTime = (seconds) => {
  const mins = Math.floor(seconds / 60)
  const secs = seconds % 60
  if (mins === 0) {
    return `${secs}s`
  }
  return secs === 0 ? `${mins}m` : `${mins}m ${secs}s`
}

// Export workouts
const exportWorkouts = async () => {
  const allWorkouts = await getAllWorkouts()
  const exportData = JSON.stringify(allWorkouts, null, 2)
  const blob = new Blob([exportData], { type: 'application/json' })
  const url = URL.createObjectURL(blob)
  const date = new Date().toISOString().split('T')[0]
  
  const link = document.createElement('a')
  link.href = url
  link.download = `workout-timer-export-${date}.json`
  document.body.appendChild(link)
  link.click()
  document.body.removeChild(link)
  URL.revokeObjectURL(url)
}

// Import workouts
const handleImport = async (event) => {
  const file = event.target.files[0]
  if (!file) return

  try {
    const content = await file.text()
    const importedWorkouts = JSON.parse(content)
    
    // Validate imported data structure
    if (!Array.isArray(importedWorkouts)) {
      throw new Error('Invalid workout data format')
    }

    // Delete all existing workouts
    const existingWorkouts = await getAllWorkouts()
    for (const workout of existingWorkouts) {
      await deleteWorkoutFromDB(workout.id)
    }

    // Save each imported workout
    for (const workout of importedWorkouts) {
      // Generate new IDs for imported workouts to avoid conflicts
      const newWorkout = {
        ...workout,
        id: generateId(),
        sets: workout.sets.map(set => ({
          ...set,
          id: generateId()
        }))
      }
      await saveWorkout(newWorkout)
    }

    // Refresh the workouts list
    workouts.value = await getAllWorkouts()
    
    // Reset the file input
    event.target.value = ''
    
    // Show success modal
    importedCount.value = importedWorkouts.length
    showImportSuccess.value = true
  } catch (error) {
    console.error('Import error:', error)
    alert('Error importing workouts. Please check the file format.')
  }
}

const closeImportSuccess = () => {
  showImportSuccess.value = false
  importedCount.value = 0
}

const startWorkout = (workout) => {
  emit('start-workout', workout)
}
</script> 