<template>
  <div class="fixed inset-0 bg-gray-500 bg-opacity-75 flex items-center justify-center p-4">
    <div class="bg-white rounded-lg shadow-xl w-full max-w-2xl max-h-[90vh] overflow-hidden">
      <!-- Header -->
      <div class="px-6 py-4 border-b border-gray-200">
        <div class="flex items-center justify-between">
          <h2 class="text-xl font-display font-semibold">
            {{ workout.name ? 'Edit Workout' : 'New Workout' }}
          </h2>
          <button @click="$emit('close')" class="text-gray-400 hover:text-gray-500">
            <XIcon class="h-6 w-6" />
          </button>
        </div>
      </div>

      <!-- Content -->
      <div class="px-6 py-4 max-h-[calc(90vh-8rem)] overflow-y-auto">
        <!-- Workout Name -->
        <div class="mb-6">
          <label class="block text-sm font-medium text-gray-700 mb-1">
            Workout Name
          </label>
          <input
            type="text"
            v-model="workoutData.name"
            class="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary focus:border-primary"
            placeholder="Enter workout name"
          >
        </div>

        <!-- Items List (Exercises and Rests) -->
        <div class="space-y-4">
          <draggable
            v-model="workoutData.sets"
            item-key="id"
            handle=".drag-handle"
            :animation="200"
            ghost-class="ghost-item"
            drag-class="dragging-item"
            class="space-y-4"
          >
            <template #item="{ element: item, index }">
              <div class="bg-gray-50 p-4 rounded-lg relative group">
                <!-- Drag Handle -->
                <div class="drag-handle absolute left-2 top-1/2 -translate-y-1/2 cursor-move text-gray-400">
                  <MenuIcon class="h-5 w-5" />
                </div>

                <!-- Rest Item -->
                <div v-if="item.type === 'rest'" class="ml-8 flex items-center gap-4">
                  <div class="flex-grow">
                    <label class="block text-sm font-medium text-gray-700 mb-1">
                      Rest Time
                    </label>
                    <div class="flex items-center gap-2">
                      <div class="flex-1 grid grid-cols-2 gap-2">
                        <div class="relative">
                          <input
                            type="number"
                            v-model="timeInputs[item.id].minutes"
                            min="0"
                            class="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary focus:border-primary"
                            @input="updateRestTime(item)"
                          >
                          <div class="absolute right-1 top-1 bottom-1 flex flex-col">
                            <button 
                              @click="adjustTime(item, 'minutes', 1)"
                              class="flex-1 px-1 text-gray-500 hover:text-primary"
                            >
                              <ChevronUpIcon class="h-3 w-3" />
                            </button>
                            <button 
                              @click="adjustTime(item, 'minutes', -1)"
                              class="flex-1 px-1 text-gray-500 hover:text-primary"
                            >
                              <ChevronDownIcon class="h-3 w-3" />
                            </button>
                          </div>
                          <span class="absolute right-8 top-2 text-gray-500">min</span>
                        </div>
                        <div class="relative">
                          <input
                            type="number"
                            v-model="timeInputs[item.id].seconds"
                            min="0"
                            max="59"
                            class="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary focus:border-primary"
                            @input="updateRestTime(item)"
                          >
                          <div class="absolute right-1 top-1 bottom-1 flex flex-col">
                            <button 
                              @click="adjustTime(item, 'seconds', 5)"
                              class="flex-1 px-1 text-gray-500 hover:text-primary"
                            >
                              <ChevronUpIcon class="h-3 w-3" />
                            </button>
                            <button 
                              @click="adjustTime(item, 'seconds', -5)"
                              class="flex-1 px-1 text-gray-500 hover:text-primary"
                            >
                              <ChevronDownIcon class="h-3 w-3" />
                            </button>
                          </div>
                          <span class="absolute right-8 top-2 text-gray-500">sec</span>
                        </div>
                      </div>
                    </div>
                  </div>
                  <button
                    @click="removeItem(index)"
                    class="self-end h-10 flex items-center text-gray-400 hover:text-red-600"
                  >
                    <TrashIcon class="h-5 w-5" />
                  </button>
                </div>

                <!-- Exercise Item -->
                <div v-else class="ml-8 space-y-4 relative pb-10">
                  <!-- Exercise Name - Full Width -->
                  <div>
                    <label class="block text-sm font-medium text-gray-700 mb-1">
                      Exercise Name
                    </label>
                    <input
                      type="text"
                      v-model="item.name"
                      class="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary focus:border-primary"
                      placeholder="e.g. Push-ups"
                    >
                  </div>

                  <!-- Controls Row -->
                  <div class="space-y-4 sm:space-y-0 sm:grid sm:grid-cols-8 sm:gap-4">
                    <!-- Sets and Reps Row for Mobile -->
                    <div class="grid grid-cols-2 gap-4 sm:contents">
                      <!-- Sets -->
                      <div class="sm:col-span-2">
                        <label class="block text-sm font-medium text-gray-700 mb-1">
                          Sets
                        </label>
                        <div class="relative sm:w-full">
                          <input
                            type="number"
                            v-model="item.count"
                            min="1"
                            class="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary focus:border-primary"
                          >
                          <div class="absolute right-1 top-1 bottom-1 flex flex-col">
                            <button 
                              @click="item.count = Math.max(1, parseInt(item.count) + 1)"
                              class="flex-1 px-1 text-gray-500 hover:text-primary"
                            >
                              <ChevronUpIcon class="h-3 w-3" />
                            </button>
                            <button 
                              @click="item.count = Math.max(1, parseInt(item.count) - 1)"
                              class="flex-1 px-1 text-gray-500 hover:text-primary"
                            >
                              <ChevronDownIcon class="h-3 w-3" />
                            </button>
                          </div>
                        </div>
                      </div>

                      <!-- Reps -->
                      <div class="sm:col-span-2">
                        <label class="block text-sm font-medium text-gray-700 mb-1">
                          Reps
                        </label>
                        <div class="relative sm:w-full">
                          <input
                            type="number"
                            v-model="item.reps"
                            min="1"
                            class="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary focus:border-primary"
                          >
                          <div class="absolute right-1 top-1 bottom-1 flex flex-col">
                            <button 
                              @click="item.reps = Math.max(1, parseInt(item.reps) + 1)"
                              class="flex-1 px-1 text-gray-500 hover:text-primary"
                            >
                              <ChevronUpIcon class="h-3 w-3" />
                            </button>
                            <button 
                              @click="item.reps = Math.max(1, parseInt(item.reps) - 1)"
                              class="flex-1 px-1 text-gray-500 hover:text-primary"
                            >
                              <ChevronDownIcon class="h-3 w-3" />
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>

                    <!-- Rest Between Sets -->
                    <div class="sm:col-span-4">
                      <label class="block text-sm font-medium text-gray-700 mb-1">
                        Rest Between Sets
                      </label>
                      <div class="flex items-center gap-2">
                        <div class="flex-1 grid grid-cols-2 gap-2">
                          <div class="relative">
                            <input
                              type="number"
                              v-model="timeInputs[item.id].minutes"
                              min="0"
                              class="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary focus:border-primary"
                              @input="updateRestTime(item)"
                            >
                            <div class="absolute right-1 top-1 bottom-1 flex flex-col">
                              <button 
                                @click="adjustTime(item, 'minutes', 1)"
                                class="flex-1 px-1 text-gray-500 hover:text-primary"
                              >
                                <ChevronUpIcon class="h-3 w-3" />
                              </button>
                              <button 
                                @click="adjustTime(item, 'minutes', -1)"
                                class="flex-1 px-1 text-gray-500 hover:text-primary"
                              >
                                <ChevronDownIcon class="h-3 w-3" />
                              </button>
                            </div>
                            <span class="absolute right-8 top-2 text-gray-500">min</span>
                          </div>
                          <div class="relative">
                            <input
                              type="number"
                              v-model="timeInputs[item.id].seconds"
                              min="0"
                              max="59"
                              class="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary focus:border-primary"
                              @input="updateRestTime(item)"
                            >
                            <div class="absolute right-1 top-1 bottom-1 flex flex-col">
                              <button 
                                @click="adjustTime(item, 'seconds', 5)"
                                class="flex-1 px-1 text-gray-500 hover:text-primary"
                              >
                                <ChevronUpIcon class="h-3 w-3" />
                              </button>
                              <button 
                                @click="adjustTime(item, 'seconds', -5)"
                                class="flex-1 px-1 text-gray-500 hover:text-primary"
                              >
                                <ChevronDownIcon class="h-3 w-3" />
                              </button>
                            </div>
                            <span class="absolute right-8 top-2 text-gray-500">sec</span>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>

                  <!-- Action Buttons -->
                  <div class="absolute bottom-0 right-0 flex gap-2">
                    <button
                      @click="duplicateExercise(index)"
                      class="text-gray-400 hover:text-primary transition-colors"
                      title="Duplicate exercise"
                    >
                      <DuplicateIcon class="h-5 w-5" />
                    </button>
                    <button
                      @click="removeItem(index)"
                      class="text-gray-400 hover:text-red-600 transition-colors"
                      title="Delete exercise"
                    >
                      <TrashIcon class="h-5 w-5" />
                    </button>
                  </div>
                </div>
              </div>
            </template>
          </draggable>

          <!-- Add Buttons -->
          <div class="flex gap-4">
            <button
              @click="addExercise"
              class="w-1/2 py-2 px-4 border-2 border-dashed border-gray-300 rounded-lg text-secondary hover:border-primary hover:text-primary flex items-center justify-center"
            >
              <PlusIcon class="h-5 w-5 mr-2" />
              Add Exercise
            </button>
            <button
              @click="addRest"
              class="w-1/2 py-2 px-4 border-2 border-dashed border-gray-300 rounded-lg text-secondary hover:border-primary hover:text-primary flex items-center justify-center"
            >
              <PlusIcon class="h-5 w-5 mr-2" />
              Add Rest
            </button>
          </div>
        </div>
      </div>

      <!-- Footer -->
      <div class="px-6 py-4 border-t border-gray-200 flex justify-end space-x-3">
        <button
          @click="$emit('close')"
          class="px-4 py-2 text-sm font-medium text-gray-700 hover:text-gray-500"
        >
          Cancel
        </button>
        <button
          @click="save"
          class="px-4 py-2 text-sm font-medium text-white bg-primary hover:bg-primary-dark rounded-md shadow-sm"
        >
          Save Workout
        </button>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, watch } from 'vue'
import { XIcon, MenuIcon, PlusIcon, TrashIcon, ChevronUpIcon, ChevronDownIcon, DuplicateIcon } from '@heroicons/vue/solid'
import draggable from 'vuedraggable'
import { saveWorkout as saveWorkoutToDb } from '../utils/storage'

// Props
const props = defineProps({
  workout: {
    type: Object,
    required: true
  }
})

// Emits
const emit = defineEmits(['save', 'close'])

// Create a reactive copy of the workout data
const workoutData = ref({
  ...props.workout,
  sets: props.workout.sets.map(set => ({
    ...set,
    type: set.type || 'exercise'
  }))
})

// Time inputs state
const timeInputs = ref({})

// Initialize time inputs when workout data changes
watch(() => workoutData.value.sets, (sets) => {
  sets.forEach(item => {
    if (!timeInputs.value[item.id]) {
      timeInputs.value[item.id] = {
        minutes: Math.floor(item.restTime / 60),
        seconds: item.restTime % 60
      }
    }
  })
}, { immediate: true })

// Update rest time when minutes or seconds change
const updateRestTime = (item) => {
  const input = timeInputs.value[item.id]
  // Ensure values are numbers and within valid ranges
  input.minutes = Math.max(0, parseInt(input.minutes) || 0)
  input.seconds = Math.min(59, Math.max(0, parseInt(input.seconds) || 0))
  // Update the total seconds in the item
  item.restTime = (input.minutes * 60) + input.seconds
}

// Adjust time with increment/decrement buttons
const adjustTime = (item, field, amount) => {
  const input = timeInputs.value[item.id]
  if (field === 'minutes') {
    input.minutes = Math.max(0, input.minutes + amount)
  } else {
    input.seconds = (input.seconds + amount + 60) % 60
    if (input.seconds < 0) input.seconds = 45
    if (input.seconds > 45) input.seconds = 0
  }
  updateRestTime(item)
}

// Methods
const addExercise = () => {
  const id = Date.now()
  workoutData.value.sets.push({
    id,
    type: 'exercise',
    name: '',
    reps: 1,
    count: 1,
    restTime: 30
  })
  timeInputs.value[id] = {
    minutes: 0,
    seconds: 30
  }
}

const addRest = () => {
  const id = Date.now()
  workoutData.value.sets.push({
    id,
    type: 'rest',
    restTime: 60
  })
  timeInputs.value[id] = {
    minutes: 1,
    seconds: 0
  }
}

const removeItem = (index) => {
  const item = workoutData.value.sets[index]
  delete timeInputs.value[item.id]
  workoutData.value.sets.splice(index, 1)
}

const duplicateExercise = (index) => {
  const exercise = workoutData.value.sets[index]
  const duplicate = {
    ...exercise,
    id: Date.now(),
    name: `${exercise.name} (copy)`
  }
  
  // Initialize time inputs for the new exercise
  timeInputs.value[duplicate.id] = {
    minutes: Math.floor(duplicate.restTime / 60),
    seconds: duplicate.restTime % 60
  }
  
  // Insert the duplicate after the original exercise
  workoutData.value.sets.splice(index + 1, 0, duplicate)
}

const save = async () => {
  // Create a clean copy of the data without any Vue reactivity
  const cleanWorkout = {
    id: workoutData.value.id,
    name: workoutData.value.name,
    sets: workoutData.value.sets.map(item => {
      if (item.type === 'rest') {
        return {
          id: item.id,
          type: 'rest',
          restTime: Number(item.restTime)
        }
      }
      return {
        id: item.id,
        type: 'exercise',
        name: item.name,
        reps: Number(item.reps),
        count: Number(item.count),
        restTime: Number(item.restTime)
      }
    })
  }
  
  // Save to database
  await saveWorkoutToDb(cleanWorkout)
  // Emit save event with updated workout data
  emit('save', cleanWorkout)
}
</script>

<style scoped>
.ghost-item {
  @apply border-2 border-dashed border-primary bg-primary/5 opacity-100;
}

.dragging-item {
  @apply opacity-100 shadow-lg border-2 border-primary;
}

.drag-handle {
  @apply cursor-grab active:cursor-grabbing;
}

/* Time input styles */
input[type="number"] {
  padding-right: 4rem;
  text-align: right;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
</style> 